.date-navigator {
  display: flex;
  justify-content: space-between;

  margin-bottom: 30px;
}

.date-container {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.date-controller {
  display: flex;
  flex-direction: row;
  gap: 5px;

  &_text {
    display: flex;
    justify-content: center;
    width: 140px;
  }

  &_button {
    width: 16px;
    height: 16px;
    border-radius: 50%;

    &:hover {
      cursor: pointer;
    }
  }
}

.calendar-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &_button:hover {
    cursor: pointer;
  }

  &_input {
    visibility: hidden;
  }
}